import { Link } from "gatsby"
import React from "react"
import styled from "@emotion/styled"
import Img from "gatsby-image"
import { Location } from "@reach/router"
import headerbg from "../images/headerbg.svg"
import Search from "../components/Search"
import {
  BrowserView,
  MobileView,
  isMobile,
} from "react-device-detect"
import { ReactComponent as MenuSeparator } from "../images/separator_down.svg"
import { ReactComponent as ExpandBtnImg } from "../images/pointer.svg"
import color from "./color"

const searchIndices = [
  { name: `Posts`, title: `Blog Posts`, hitComp: `PostHit` },
]

const SearchBar = styled.div`
  @media only screen and (max-width: 768px) {
    position: fixed;
    top: 0;
    right: calc(-100vw + 50px);
    z-index: 10;
    height: 35px;
    width: 100vw;
    background-color: ${color.background_base};
    border-radius: 3px;
    text-align: center;
    padding: 5px 0 0;
    transform: ${props => (props.active ? `translateX(calc(50px - 100vw))` : `translateX(0)`)};
    transition: transform 0.3s ease-out;
     
    div {
      grid-gap: 5px;
    }
  
    form {
      text-align: center;
      input {
        margin-right: 0;
        width: calc(100vw - 50px);
        border: 2px solid ${color.border};
      }
    }
  }
  @media only screen and (min-width: 768px) {
    position: absolute;
    top: 0;
    right: 0;
    margin: 10px 0;
    z-index: 10;
  }
  

`

const MobileSearchBarCap = styled.div`
  height: 34px;
  width: 10px;
  background-color: ${color.background_base};
  position: fixed;
  top: 5px;
  right: 0;
  z-index: 10;
  border-left: 2px solid ${color.border};
  display: inline-block;
  @media only screen and (min-width: 768px) {
    display: none;
  }
`

const MobileExtendSearchButton = styled.div`
    width: 30px;
    height: 30px;
    position: absolute;
    z-index: 2;
    top: 5px;
    left: 8px;
    border: 2px solid ${color.border};
    border-right: 0;
    @media only screen and (min-width: 768px) {
      display: none;
    }
`

const THeader = styled.header`
  width: 100%;
  background-color: ${color.background_base};
  z-index: 4;
  border-bottom: 1px solid ${color.border};
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;
  @media only screen and (min-width: 768px) {
    min-width: 1000px;
  }
`

const LinkRow = styled.div`
  height: 40px;
  position: relative;
  background-color: ${color.background_base};
  text-align: center;
     
  @media only screen and (min-width: 768px) {
    height: 49px;
    width: 624px;
    overflow: hidden;
    display: inline-block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%) scale(0.8);
  }
  
  @media only screen and (min-width: 1400px) {
      transform: translateX(-50%);
  }
`

const MainLink = styled.h1`
  display: block;
  border-bottom: 1px solid ${color.border};
  margin: 0;
  padding: 3px 10px;
  position:relative;
  z-index: 10;
  height: 40px;
  background-color: ${color.background_base};
    
  @media only screen and (min-width: 768px) {
    display: inline-block;
    margin: 0 8px;
    font-size: 30px;
    border-bottom: 0;
  }
`

export const BrandIcon = styled(Img)`
  width: 35px;
  display: inline-block;
  vertical-align: middle;
`

const HeaderUl = styled.ul`
  margin: 5px 0 1px;
  padding: 0;
  position:relative;
  z-index: 3;
`

const HeaderLi = styled.li`
  position: relative;
  width: 100vw;
  background-color: ${color.background_base};
  font-size: larger;
  padding: 0;
  display: block;
  text-align: center;

  @media only screen and (min-width: 768px) {
    background-image: url(${headerbg});
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-color: initial;
    display: inline-block;
    font-size: 16px;
    width: 140px;
    padding: 0 8px;
  }
`

const HeaderLink = styled(Link)`
    padding: 10px;
    font-weight: ${props => (props.active ? `600` : `400`)};
    text-transform: uppercase;
    line-height: 42px;
    letter-spacing: .7px;
    color: ${props => (props.active ? color.link_active : color.link)};
    border: 0;
    &:hover {
      color: ${color.link_hover};
    }
`

const LinkShadow = styled.div`
  @media only screen and (min-width: 768px) {
    width:160px;
    height: 40px;
    position: absolute;
    top:6px;
    transform:  ${props =>
  (contains(props.path, ["/blog", "/post", "/category"]) ? `translateX(155px)` :
    (props.path.includes("/aboutme") ? `translateX(310px)` :
      (props.path.includes("/sandbox") ? `translateX(464px)` :
          `translateX(0.6px)`
      )))};
    transition: transform 0.2s ease-out;
  }
`

const SubLinkShadow = styled.div`
  background-color: ${color.link_active};
  height: 22px;
  padding: 0;
  margin: 0;
  position: absolute;
  width: 120px;
`

const SubLinkShadow1 = styled(SubLinkShadow)`
  top: 0;
  left: 0;
`

const SubLinkShadow2 = styled(SubLinkShadow)`
  bottom: 0;
  right: 0;
`

const HeaderTitle = styled.span`
  vertical-align: middle;
  font-size: 35px;
  line-height: 35px;
`

const Banner = styled(Link)`
  border: none;
`


const ExpandButton = styled(ExpandBtnImg)`
  height: 30px;  
  width: 30px;
  z-index: 10;
  display: inline-block;
  position: absolute;
  top: 5px;
  right: 10px;
  transition: transform 0.3s ease-out;
  transform: ${props => (props.active ? `rotate(270deg)` : `rotate(90deg)`)};
`

const HeaderMenu = styled(Link)`
  width: 100vw;
  height: 36px;
  display: block;
  font-size: 24px;
  line-height: 36px;
  color: ${props => (props.active ? color.link_active : color.link)};
  background-color: ${color.background_base};
  transition: transform 0.3s ease-out;
  position: absolute;
  top: 0;
  left: 0; 
`
const MobileMenuSlider = styled.div`
  position: relative;
  width: 100vw;
  .lnk {
    transform: translateY(-100%);
     transition: transform 0.3s ease-out;
    ${props =>
  (contains(
    props.path, ["/blog", "/post", "/category"]) ? `&.blog{transform: translateY(0);}` :
    (props.path.includes("/aboutme") ? `&.about{transform: translateY(0);}` :
      (props.path.includes("/sandbox") ? `&.sandbox{transform: translateY(0);}` :
          `&.home{transform: translateY(0);}`
      )))};
         
    ${props => (props.active ? `
      &.home {
        transform: translateY(0);
      }
      &.blog {
        transform: translateY(100%);
      }
      &.about {
        transform: translateY(200%);
      }
      &.sandbox {
        transform: translateY(300%);
      }
      &.separator {
        transform: translateY(108px);
      }
    ` : ``)};
  }
  
  .separator {
    transform: ${props => (props.active ? `translateY(108px)` : `translateY(0)`)};
  }
  
`

const Separator = styled(MenuSeparator)`
  width: 100vw;
  display: block;
  transition: transform 0.3s ease-out;
  background-color: ${color.background_base};
  position: absolute;
  top: 36px;
  left: 0;
`

function contains(target, pattern) {
  let value = 0
  pattern.forEach(function(word) {
    value = value + target.includes(word)
  })
  return (value === 1)
}

class HeaderElement extends React.Component {
  constructor(props) {
    super(props);
    this.state = {isExpanded: false, isSearchMode: false};
    this.handleClick = this.handleClick.bind(this);
  }

  state = {
    isExpanded: false,
    isSearchMode: false,
  }

  preventClick = (e, currentPath) => {
    if (currentPath === 1) {
      e.preventDefault()
      e.stopPropagation()
      e.nativeEvent.stopImmediatePropagation()
    }
  }

  // function to handle the click
  expandClick = (e, currentPath, canExpand) => {
    if (canExpand || this.state.isExpanded ===true){
      this.setState(state => ({ isExpanded: !state.isExpanded }))
    }
    if (this.state.isExpanded === false || currentPath === 1) {
      this.preventClick(e, currentPath)
    }
  }

  expandSearch = () => {
    if (isMobile) {
      this.setState(state => ({ isSearchMode: true }))
    }
  }

  foldSearch = () => {
    if (isMobile) {
      this.setState(state => ({ isSearchMode: false }))
    }
  }

  render() {
    let data = this.props
    return (
      <>
        <THeader>
          <MainLink>
            <Banner to="/">
              <BrandIcon fluid={data.siteIcon.childImageSharp.fluid}/>
              <HeaderTitle>{data.siteTitle}</HeaderTitle>
            </Banner>
          </MainLink>
          <LinkRow active={this.state.isExpanded}>
            <Location>
              {({ location }) => {
                return (
                  <>
                    <BrowserView>
                      <HeaderUl path={location.pathname}>
                        <HeaderLi>
                          <HeaderLink active={location.pathname === "/" ? 1 : 0} to="/" onClick={(e) => {
                            this.preventClick(e, location.pathname === "/" ? 1 : 0)
                          }}>Home</HeaderLink>
                        </HeaderLi>
                        <HeaderLi>
                          <HeaderLink active={(contains(location.pathname, ["/blog", "/post", "/category"])) ? 1 : 0}
                                      to="/blog" onClick={(e) => {
                            this.preventClick(e, location.pathname === "/blog" ? 1 : 0)
                          }}>Blog</HeaderLink>
                        </HeaderLi>
                        <HeaderLi>
                          <HeaderLink active={location.pathname.includes("/aboutme") ? 1 : 0} to="/aboutme"
                                      onClick={(e) => {
                                        this.preventClick(e, location.pathname === "/aboutme" ? 1 : 0)
                                      }}>About Me</HeaderLink>
                        </HeaderLi>
                        <HeaderLi>
                          <HeaderLink active={location.pathname.includes("/sandbox") ? 1 : 0} to="/sandbox"
                                      onClick={(e) => {
                                        this.preventClick(e, location.pathname === "/sandbox" ? 1 : 0)
                                      }}>Sandbox</HeaderLink>
                        </HeaderLi>
                      </HeaderUl>
                      <LinkShadow path={location.pathname}>
                        <SubLinkShadow1/>
                        <SubLinkShadow2/>
                      </LinkShadow>
                    </BrowserView>
                    <MobileView>
                      <MobileMenuSlider active={this.state.isExpanded ? 1 : 0} path={location.pathname}>
                        <HeaderMenu className="lnk sandbox" to="/sandbox" onClick={(e) => {
                          this.expandClick(e, location.pathname === "/sandbox" ? 1 : 0, 1)
                        }}>Sandbox</HeaderMenu>
                        <HeaderMenu className="lnk about" to="/aboutme" onClick={(e) => {
                          this.expandClick(e, location.pathname === "/aboutme" ? 1 : 0, 1)
                        }}>About Me</HeaderMenu>
                        <HeaderMenu className="lnk blog" to="/blog" onClick={(e) => {
                          this.expandClick(e, location.pathname === "/blog" ? 1 : 0, contains(location.pathname,["/post","/category","/blog/"])?0:1)
                        }}>Blog</HeaderMenu>
                        <HeaderMenu className="lnk home" to="/" onClick={(e) => {
                          this.expandClick(e, location.pathname === "/" ? 1 : 0, 1)
                        }}>Home</HeaderMenu>
                        <ExpandButton active={this.state.isExpanded ? 1 : 0} onClick={(e) => {
                          this.expandClick(e, 0,1)
                        }}/>
                        <Separator className={"separator"}/>
                      </MobileMenuSlider>
                    </MobileView>
                  </>
                )
              }}
            </Location>
          </LinkRow>
        </THeader>
        {(data.pageContext.layout === "blog" || data.pageContext.layout === "post")  && (
          <>
            <SearchBar active={this.state.isSearchMode?1:0}>
              <MobileExtendSearchButton onClick={this.expandSearch} isVisible={!this.state.isSearchMode}/>
              <Search collapse indices={searchIndices} foldSearch={this.foldSearch} isInPost={(data.pageContext.layout === "post"?1:0)} />
            </SearchBar>
            <MobileSearchBarCap/>
          </>
        )}
      </>
    )
  }
}

export default HeaderElement
